	import Head from "../public/head.vue"
  import Bus from '../assets/eventBus'
	export default {
		data() {
			return {
				activeIndex:'1',
				isBasic:false,
       			img:'./static/img/home/4.jpg',
				hotList:[],
				search:'',
				allText:{
					title:[
						{
							'zh':'故事',
							'en':'story',
							'ja':'物語'
						},
						{
							'zh':'搜索故事',
							'en':'Search stories',
							'ja':'ストーリーを検索'
						},
						{
							'zh':'精选故事',
							'en':'Featured stories',
							'ja':'特集記事'
						},
						{
							'zh':'户外活动',
							'en':'Outdoor activities',
							'ja':'野外活動'
						},
						{
							'zh':'少数民族',
							'en':'minority',
							'ja':'少数'
						},
						{
							'zh':'本土文化',
							'en':'local culture',
							'ja':'地域文化'
						},
						{
							'zh':'请输入搜索内容',
							'en':'Enter search content',
							'ja':'検索内容を入力'
						},
						{
							'zh':'热门目的地',
							'en':'Popular destination',
							'ja':'人気の目的地'
						},
						{
							'zh':'推荐搜索',
							'en':'Recommended search',
							'ja':'おすすめの検索'
						},
					]
				}
			};
		},
		components:{
			Head,
		},
		computed:{
			Lan(){
				return this.Store.getters.getValue
			}
		},
		methods:{
		  sosuo(){
		    this.isBasic = !this.isBasic
      },
			handleSelect(key){
				this.activeIndex=key;
				if(key=='1'){
					this.$router.push('/wonderful')
				}else if(key=='2'){
					this.$router.push('/outdoor')
				}else if(key=='3'){
					this.$router.push('/ethnic')
				}else if(key=='4'){
					this.$router.push('/local')
				}
			},
      toSearch(){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: this.search,
            type:1
          }
        })
      },
      searchStory(item,index){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: item.city,
            type:1
          }
        })
      }
		},
    mounted() {
		  window.scroll(0,0)
      Bus.$on('hotPlace',(msg)=>{
        this.hotList = msg
      })
    },
    destroyed(){

    }
  }



